.form-input {
    outline: none;
    background: none;
    border: 1px solid #8758ff;
    padding: 0.5rem 1rem;
    margin-top: 1rem;
    margin-bottom: 2rem;
    width: 300px;
    color: #fff;
}

.form-input::placeholder {
    color: #ffffff4d;
}
button {
    background: #8758ff;
    color: #fff;
    border: none;
    padding: 0.55rem;
    cursor: pointer;
}
.form {
    background: #1A1A40;
    padding: 2rem;
    padding-right: 100px;
  }
  