.todo-text {
    font-weight: bold;
    background: none;
    color: #fff;
    border: none;
}

.todo-text.done {
    font-weight: 300;
    text-decoration: line-through;
    color: rgb(184, 174, 174);
}

.todo-text.edit {
    background: rgb(98, 89, 89);
    color: aliceblue;
}

.settings {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-image: url(../Todo/icons8-menu\ \(2\).svg);
    background-position: center;
    background-size: cover;
    position: relative;
}
.check{
    border-radius: 50%;
}
.edit-btn{
    border-radius: 50%;
}
.delete{
    border-radius: 50%;
}

.settings button {
    display: none;
}

.settings.active button {
    display: block;
    position: absolute;
    width: 40px;
    height: 40px;
    left: 30px; 
    margin-top: 25px;
}

.settings.active button:first-child {
    top: -72px;
}

.settings.active button:nth-child(2) {
    top: -37px;
    right: -100%;
    margin-left: 32px;
}

.settings.active button:nth-child(3) {
    top: 0;
    right: -100%;
}

.todo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #1A1A40;
    color: #fff;
    padding: 0.75rem 1rem;
    margin-bottom: 1rem;
    cursor: pointer;
    padding-right: 100px;
    padding-bottom: 50px;
}
